
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Countup from 'vux/src/components/countup/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Blur from 'vux/src/components/blur/index.vue'

import ViewBox from '../../node_modules/vux/src/components/view-box/index.vue'
import * as types from '../store/mutation'

export default {

directives: {
TransferDom
},
components: {
ViewBox,
XHeader,
XButton,
Box,
XInput,
Group,
Cell,
Toast,
Tabbar,
TabbarItem,
Flexbox,
FlexboxItem,
Divider,
Countup,
Swiper,
SwiperItem,
Confirm,
XSwitch,
Datetime,
Blur
},
computed: {
leftOptions () {
return {
showBack: true,
backText: ''
}
},
rightOptions () {
return {
showMore: false
}
}
},
mounted () {
this.getUserInfo()
this.failureShowToast = false
this.changeSuccessShow = false
},
data () {
return {
title: '个人中心',
ownPic: '../assets/dialog/01.jpg',
url: '../assets/dialog/01.jpg',
showConfirmBox: false,
computeDaysValue: '',
successShow: false,
showText: '',
username: '',
score: 0,
computeHoursFunction (date, isToday, generateRange) {
if (isToday) {
return generateRange(new Date().getHours(), 23)
} else {
return generateRange(0, 23)
}
},
computeDaysFunction (options, generateRange) {
return [options.month] 
},
mobile: '',
password: '',
iconType: '',
successText: '',
changeSuccessShow: false,
changeFailureShow: false,
startCount: 0,
endCount: 100,
totalPages: 1002,
failureText: ''
}
},
methods: {
onHide () {
this.$router.push('/')
},
goRegulation () {
this.$router.push('regulation')
},
goQuestion () {
this.showConfirmBox = true
},
onIndexChange (newIndex, oldIndex) {
console.log(newIndex, oldIndex)
},
onCancel () {

},
onConfirm () {
this.$router.push('question')
},
onShow () {

},
change (val) {
console.log('on change', val)
},
onBlur (val) {
console.log('on blur', val)
},
onFocus (val, $event) {
console.log('on focus', val, $event)
},
onEnter (val) {
console.log('click enter!', val)
},
getUserInfo () {
let userId = window.localStorage.getItem('userId')
this.$http.post(this.$apis.userInfo, {userId: userId})
.then((res) => {
if (res.code === 200) {
this.score = res.data.score
this.username = res.data.username
}
})
.catch(function (error) {
console.log(error)
})
},
logout () {
this.$http.post(this.$apis.logout).then(res => {
if (res.code === 200) {
this.$store.commit(types.LOGOUT)
this.showText = res.msg
this.successShow = true
} else {
this.showText = res.msg
this.successShow = true
}
})
}
}
}



